@import url(https://fonts.googleapis.com/css2?family=Lora&family=Ubuntu:wght@300;700&display=swap);
/* 
  font-family: 'Lora', serif;
  font-family: 'Ubuntu', sans-serif;
*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 18px;
}

body {
  font-family: 'Ubuntu', sans-serif;
}

a {
  text-decoration: none;
  
}

h1, h2 {
  font-family: 'Lora', serif;
}

p {
  font-family: 'Ubuntu', sans-serif;
}

hr {
  outline: none;
  background: #707070;
  height: 1px;
  margin: 2em 0;
}

.container {
  display: flex;
  max-width: 1300px;
  margin: 0 auto;
}

.container-wrapper {
  margin-top: 3.22em;
}

.active-link {
  border-bottom: 2px solid #707070;
}

/* Home component css class */
.sidebar-container {
  margin-left: 2.83em;
  flex-basis: 25%;
}

.section-wrapper {
  flex-basis: 100%;
}


/* Mobile styles for responsive design */

@media screen and (max-width: 600px) {
  html {
    font-size: 12px;
  }

  .container {
    flex-direction: column;
    padding: 0 1em;
  }

  .sidebar-container {
    margin: 0;
    width: 100%;
  }
}
nav {
    height: 20vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    margin-right: auto;
}

.logo h1 {
    font-weight: 400;
    font-size: 1.83rem;
    color: #143774;
}

.logo p {
    letter-spacing: 0.13rem;
    font-weight: 700;
    font-size: 0.66rem;
    color: #1792D2;
}

nav ul {
    display: flex;
    list-style: none;
}

nav li {
    letter-spacing: 0.17rem;
    font-weight: 700;
}

nav a {
    color: #717171;
    margin-left: 1.5em;
    padding-bottom: 0.5em;
}

nav a:hover {
    border-bottom: 2px solid #707070;
}


/* Mobile styles for responsive design */

@media screen and (max-width: 600px) {
    nav, nav ul {
        flex-direction: column;
        text-align: center;
        justify-content: center;
    }

    nav {
        height: 25vh;
    }

    nav li {
        margin-top: 1.5em;
    }

    .logo{
        margin: 0;
    }

}

.footer {
    height: 15vh;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.footer h1, .footer h2 {
    font-family: 'Ubuntu', sans-serif;
    color: #FFFFFF;
    opacity: 0.5;
    font-size: 1rem;
    line-height: 1.55rem;
}

.footer h1 {
    font-weight: 700;
    
}

.footer h2 {
    font-weight: 300;
}

.title-img {
    width: 100%;
    height: 50vh;
    object-fit: cover;
    -webkit-mask-size: cover;
    mask-size: cover;
}

.txt-comment-date {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
    font-size: 0.77rem;
    color: #707070;
    margin: 1.44em 0;
}

.txt-header {
    color: #143774;
    font-size: 1.33rem;
    font-weight: 400;
    letter-spacing: 0.16rem;
    margin-bottom: 1.44em;
}

.txt-content {
    font-size: 1rem;
    font-weight: 300;
    letter-spacing: 0.1rem;
    margin-bottom: 1.44em;
    line-height: 1.55rem;
}

.txt-content a {
    color: #1792D2;
}

.txt-content a:hover{
    text-decoration: underline;
}

.txt-continue a{
    color: #1792D2;
    letter-spacing: 0.15rem;
    font-size: 0.77rem;
    font-weight: 700;
}

/* Mobile styles for responsive design */

@media screen and (max-width: 600px) {
    .title-img {
        height: 30vh;
    }
}

.sidebar-wrapper {
    display: flex;
    background: #EBEBEB;
    margin-bottom: 2em;
    flex-wrap: wrap;
}

.sidebar-about-wrapper, .sidebar-about {
    width: 100%;
    padding: 1em;
}

.sidebar-about {
    background: white;
}

.sidebar-about h1, .sidebar-about p {
    font-family: "Ubuntu", sans-serif;
    color: #143774;
}

.sidebar-about h1 {
    font-weight: 700;
    letter-spacing: 0.2rem;
    font-size: 1rem;
    margin-bottom: 1.44em;
}

.sidebar-about p {
    font-size: 0.77rem;
    line-height: 1.33rem;
    font-weight: 300;
}

.sidebar-about img {
    width: 100%;
    height: 26.5vh;
    object-fit: cover;
    -webkit-mask-size: cover;
    mask-size: cover;
    margin-bottom: 1.44em;
}

.sidebar-about .side-img {
    height: 10vh;
}

.sidebar-about .side-title {
    color: #143774;
    font-size: 1rem;
    line-height: 1.55rem;
    font-weight: 400;
}


/* Mobile styles for responsive design */

@media screen and (max-width: 600px) {

}
.section-content-wrapper {
   display: flex;
   margin-top: 1.44em 0;
   margin-bottom: 4em;
}

.section-title-left {
    margin-right: 1.44em;
}

.section-img {
   width: 11em;
   height: 13.44em;
}


/* Mobile styles for responsive design */

@media screen and (max-width: 600px) {

    .section-content-wrapper {
        flex-direction: column;
    }

    .section-title-left {
        margin: 0;
    }

    .section-img {
        height: 30vh;
        width: 100%;
    }
    
}
